import React, {Component} from 'react';
import {connect} from 'react-redux';

import {Row, Col} from 'antd';

import marked from 'marked'
import hljs from 'highlight.js';
import html2markdown from 'html2markdown';


hljs.configure({
  tabReplace: '  ',
  classPrefix: 'hljs-',
  languages: ['CSS', 'HTML, XML', 'JavaScript', 'PHP', 'Python', 'Stylus', 'TypeScript', 'Markdown']
})
marked.setOptions({
  highlight: (code) => hljs.highlightAuto(code).value,
  gfm: true,
  tables: true,
  breaks: false,
  pedantic: false,
  sanitize: true,
  smartLists: true,
  smartypants: false
});

class Edit extends Component {
  constructor(props) {
    super(props)
    this.state = {
      previewContent: '',
      aceBoxH: null

    }

    this.cacheValue()
    this.containerScroll = this.containerScroll.bind(this)
    this.onContentChange = this.onContentChange.bind(this)
  }

  componentWillMount() {
    const {editCont=''} = this.props;

    this.setState({
      previewContent:decodeURIComponent(editCont)
    })
  }
  componentDidMount() {
    this.setState({
      aceBoxH: document.documentElement.clientHeight - document.querySelector('.editor-main-a').offsetHeight + 'px'
    })
  }

  cacheValue() {
    this.currentTabIndex = 1
    this.hasContentChanged = false
    this.scale = 1
  }

  setCurrentIndex(index) {
    this.currentTabIndex = index
  }

  containerScroll(e) {
    this.hasContentChanged && this.setScrollValue()
    if (this.currentTabIndex === 1) {
      this.previewContainer.scrollTop = this.editContainer.scrollTop * this.scale
    } else {
      this.editContainer.scrollTop = this.previewContainer.scrollTop / this.scale
    }
  }

  onContentChange(e) {
    let markCont=marked(e.target.innerText)
    const {handleChangeMarkEdit} = this.props;
    handleChangeMarkEdit(markCont)
    this.setState({
      previewContent: markCont
    })
    !this.hasContentChanged && (this.hasContentChanged = true)
  }

  setScrollValue() {
    // 设置值，方便 scrollBy 操作
    this.scale = (this.previewWrap.offsetHeight - this.previewContainer.offsetHeight) / (this.editWrap.offsetHeight - this.editContainer.offsetHeight)
    this.hasContentChanged = false
  }

  render() {

    let state = this.state
    return (
      <div>
        <div className="editor-main-a" style={{height: state.aceBoxH}}
             key='main'>
          <Row>
            <Col span={12}>
              <div style={{height: state.aceBoxH}}>
                <div className="  content-edit" onMouseOver={this.setCurrentIndex.bind(this, 1)}
                     onScroll={this.containerScroll} ref={node => this.editContainer = node}>
                  <div contentEditable="plaintext-only"
                       className="common-wrapper " onInput={this.onContentChange}
                       dangerouslySetInnerHTML={{__html:html2markdown(state.previewContent)}}
                       ref={node => this.editWrap = node}></div>
                </div>
              </div>
            </Col>
            <Col span={12}>
              <div>
                <div className="  content-edit" ref={node => this.previewContainer = node}
                     onMouseOver={this.setCurrentIndex.bind(this, 2)} onScroll={this.containerScroll}>
                  <div className=" common-wrapper" ref={node => this.previewWrap = node}
                       dangerouslySetInnerHTML={{__html: state.previewContent}}></div>
                </div>
              </div>
            </Col>
          </Row>

        </div>
        <style jsx>{`
        .content-edit{
          max-height:600px;
          height:600px;
          overflow-y:scroll;
          border: 1px solid #ddd;
          border-top:none;
        }
.editor-main-a  .common-wrapper {
  padding: 20px;
  min-height: 100%;
  outline: none;
}


        `}</style>
      </div>

    );
  }

}

export default connect()(Edit);


